:root {
  --container-max-width: 120rem;
  --container-padding: 1rem;

  --nav-height: 9.1rem;

  --box-shadow: 0 2rem 2rem rgba(0, 0, 0, 0.2);

  --clr-black--1: hsl(0, 0%, 5%);
  --clr-black--2: hsl(0, 0%, 10%);
  --clr-black--3: hsl(0, 0%, 15%);
  --clr-purple: hsl(245, 49%, 64%);
  --clr-red: hsl(0, 60%, 50%);
  --clr-white: hsl(0, 0%, 100%);
  --clr-blue: hsl(229, 76%, 66%);

  --fw-reg: 400;
  --fw-bold: 900;

  --fs-h1: 3.5rem;
  --fs-h2: 3rem;
  --fs-h3: 2.5rem;
  --fs-body: 1.5rem;

  --hamburger-color: hsl(0, 0%, 80%);
}
